
#svg-holder{
  overflow: hidden;
}
svg, path{
  -webkit-transform: all 1s;
          transform: all 1s;
}
circle{
  fill: #000;
  opacity: 0;
}

circle.pulse {
  -webkit-animation: pulse  5s linear infinite;
  animation: pulse 5s linear infinite;
}

@keyframes pulse {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes pulse {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 0;
  }
}




