

h2{
  text-align: center;
  font-family: 'proxima_novalight';
}

#home-page{
  min-height: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-align-content: center;
      -ms-flex-line-pack: center;
          align-content: center;
} 
#temperature-wrapper{
  -webkit-box-flex: 0;
  -webkit-flex: 0;
      -ms-flex: 0;
          flex: 0;
}
#file-upload{
  margin: 0px 20px;
}
#file-selection{
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
  min-width: 250px;
  -webkit-transition: all .5s;
  transition: all .5s;
}
#file-selection.is-focus{
  -webkit-box-flex: 8;
  -webkit-flex: 8;
      -ms-flex: 8;
          flex: 8;
  min-width: 70%;
}
#file-info{
  text-align: center;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-transition: all .5s;
  transition: all .5s;
}
#file-info.is-focus{
  -webkit-box-flex: 8;
  -webkit-flex: 8;
      -ms-flex: 8;
          flex: 8;
}
#file-image{
  margin: 20px auto;
}
#file-info.is-focus #file-image i{
  font-size: 3em;
  margin-bottom: 0;
  line-height: 0;
  color: black;
  position: relative;
  bottom: 30px;
}
#file-image.hide{
  width: 0;
  padding-top: 0;
}
#file-image i{
  font-size: 0px;
}
.drop-box {
  background: #F8F8F8;
  border: 5px dashed #DDD;
  width: 60%;
  height: 65px;
  text-align: center;
  padding-top: 25px;
  margin: 0px auto;
}
.drop-box.drag-over{
  background: #eee;
  border-color: #cfc;
} 
#files-page{
  position: relative;
  min-height: 100%;
}
#renderArea {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: calc(100vh - 100px);
  z-index: 0;
}

#renderArea md-progress-circular {
  position: absolute;
  z-index: -1;
  left: 50%;
  top: 50%;
  margin-left: -50px;
  margin-top: -50px;
}

#left-column{
  float: left;
}

#right-column{
  float: left;
}




